.header {
	position: relative;
	width: 100%;
	height: 100vh;
	min-height: 420px;
	display: flex;
	margin-top: 0px;
	margin-bottom: 0px;
    background: radial-gradient(66.55% 121.27% at 81.66% 67.93%, rgba(227, 8, 19, 0.15) 0%, rgba(227, 8, 19, 0) 100%);
	
	@include maxw1200 {
		height: auto;
		padding-bottom: 20px;
	}

	.container {
		display: flex;
		flex-direction: column;
	}

	&__top {
		display: flex;
		justify-content: space-between;

		@include maxw540 {
			flex-direction: column;
			margin-bottom: 20px;
		}

		&-left {
			display: flex;
			align-items: center;

			&__logo {
				margin-right: 10px;
				img {
					width: 78px;
					height: 78px;
				}
			}

			&__locale {
				position: relative;
				display: flex;
				align-items: center;
				margin-left: 8px;
				padding-left: 25px;
				min-height: 15px;
				
				&::before {
					content: "";
					position: absolute;
					left: 0px;
					top: 2px;
					width: 14px;
					height: 14px;
					-webkit-mask: url("data:image/svg+xml;base64,") left center no-repeat;
					background-color: rgb(26, 26, 26);
				}
			}
		}

		&-right {
			display: flex;
			align-items: flex-end;
			flex-direction: column;
			justify-content: center;

			@include maxw540 {
				align-items: baseline;
			}

			a {
				&:hover {
					color: var(--c-red);
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		margin-top: auto;
		margin-bottom: auto;

		@include maxw1200 {
			flex-direction: column-reverse;
		}

		&-info {
			display: flex;
			flex-direction: column;
			max-width: 550px;

			h1 {
				max-width: 500px;
				font-size: 32px;
				margin-bottom: 10px;

				@include maxw540 {
					font-size: 24px;
					max-width: 320px;
				}
			}

			p {
				font-size: 22px;
				font-weight: bold;
				margin-bottom: 20px;

				@include maxw540 {
					font-size: 18px;
					margin-bottom: 12px;
				}
			}

			ul {
				padding-left: 20px;
				max-width: 400px;
				margin-bottom: 20px;

				li {
					margin: 8px 0px;

					&::before {
						content: "•";
						color: rgb(237, 28, 36);
						font-weight: bold;
						display: inline-block;
						width: 1em;
						margin-left: -1em;
					}
				}
			}

			.button-red {
				max-width: 275px;

				@include maxw540 {
					max-width: 100%;
				}
			}
		}

		&-image {
			flex: 1;
			min-width: 0;

			max-width: 600px;

			@include maxw540 {
				margin-bottom: 10px;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}