.package {
	position: relative;
    width: 100%;
    margin-top: 100px;
    margin-bottom: 70px;

	@include maxw1200 {
		margin-top: 20px;
		margin-bottom: 20px;
	}

	&__box {
		display: flex;
		flex-direction: column;

		&-top {
			display: flex;

			@include maxw991 {
				flex-direction: column;
			}

			&__left {
				flex: 0 1 auto;

				@include maxw991 {
					margin-bottom: 32px;
				}

				p {
					font-size: 18px;
				}
			}

			&__right {
				flex: 0 1 auto;

				@include maxw991 {
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}

		&-bottom {
    		padding: 14px 0px 32px;
			margin-top: 80px;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;


			&__item {
				flex: 0 0 calc((100% - 40px) / 3);
				max-width: calc((100% - 40px) / 3);

				@include maxw991 {
					flex: 0 0 100%;
					max-width: 100%;

					margin-bottom: 20px;

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			@include maxw991 {
				margin-top: 32px;
			}
		}
	}
}