.sect_plan {

	&__wrap {
		display: flex;
		flex-direction: column;
		

		&-title {
			margin-bottom: 8px;
			color: #1A1A1A;
			font-weight: bold;
			font-size: 32px;
			line-height: 40px;
			// margin-left: auto;
			// margin-right: auto;

			@include maxw540 {
				text-align: left;
				margin-left: 0;

				font-size: 16px;
				line-height: 20px;
			}
		}

		&-desc {
			max-width: 560px;
			color: #181B21;
			font-size: 14px;
			line-height: 20px;
			// margin-left: auto;
			// margin-right: auto;

			// text-align: center;

			@include maxw540 {
				text-align: left;
				margin-left: 0;
			}
		}

		&-items {
			display: flex;
			padding: 24px 0;
			max-width: 950px;
			margin-left: 0 !important;
			margin-right: auto !important;

			margin-top: 24px;
			margin-bottom: 32px;

			@include maxw991 {
				max-width: 100%;
				margin-top: 16px;
				margin-bottom: 16px;
			}

			.item {
				position: relative;
				color: #1A1A1A;
				max-width: 304px;
				height: 468px;

				flex-direction: column;
				flex-wrap: nowrap;
				display: flex;
				overflow: hidden;
				border-radius: 16px;
				text-rendering: geometricPrecision;
				transition: var(--transition);

				margin-right: 20px;

				&:last-child {
					margin-right: 0;
				}

				&__top {
					height: 323px;
					display: flex;
					flex-direction: column;
					flex-wrap: nowrap;
					padding: 32px 24px 16px;
					color: #ffffff;

					background: url(../img/section3/bg1.jpg) 0 0 / cover repeat-y;
					background: -webkit-image-set(url(../img/section3/bg1.jpg) 1x) 0 0 / cover repeat-y;
					background: image-set(url(../img/section3/bg1.jpg) 1x) 0 0 / cover repeat-y;
					border-bottom: 1px solid #F7F7F7;
					background-color: #ffffff;

					&-title {
						display: block;
						font-size: 24px;
						line-height: 32px;
						background: none;
						-webkit-background-clip: text;
						color: inherit;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}

					&-desc {
						height: 32px;
						margin-top: 4px;
						font-size: 12px;
						line-height: 16px;
					}

					&-items {
						width: calc(100% + 32px);
						height: 32px;
						// margin: 8px -16px 4px;
						display: flex;
						-webkit-flex-wrap: nowrap;
						-ms-flex-wrap: nowrap;
						flex-wrap: nowrap;
						margin-top: 5px;

						&__left, &__right {
							border-radius: 8px;
							width: 32px;
							height: 32px;
							padding: 8px;
							background-color: #EEEEEF;
							transition: var(--transition);
							// display: flex;
							align-items: center;
							justify-content: center;
							
							display: none;

							&:hover {
								background-color: #FAFAFA;
							}

							&:active {
								transform: scale(0.98);
							}
						}

						&__left.active, &__right.active {
							display: flex;
						}

						&__box {
							position: relative;
							flex: 1 1 auto;
							margin-left: 0;
							margin-right: 16px;
							display: flex;
							flex-wrap: nowrap;
							overflow: hidden;
						}

						&__box.active {
							margin-left: 16px;
							margin-right: 0;
						}

						&__item {
							&:not(:last-child) {
								margin-right: 4px;
							}

							img {
								max-height: 32px;
								height: auto;
							}
						}
					}

					&-accordion {
						display: flex;
						flex-wrap: nowrap;
						flex-direction: column;
						// height: 144px;

						&__item {
							width: 100%;
							margin-top: 16px;

							.button {
								width: calc(100% + 32px);
								margin-left: -16px;
								margin-right: -16px;
								padding: 8px 16px;
								border-radius: 16px;
								background: rgba(255,255,255,0.2);

								display: flex;
								align-items: center;
								justify-content: space-between;

								span {
									font-size: 16px;
									line-height: 24px;
									font-weight: bold;
									color: #ffffff;
								}

								svg {
									transition: var(--transition);
									width: 40px;
									height: calc(100% + 16px);
									margin: -8px -8px -8px 4px;

									path {
										stroke: #ffffff;
									}
								}
							}

							.list {
								max-height: unset;
								padding-top: 8px;
								height: 48px;
								transition: var(--transition);
								// display: flex;
								height: 0;
								overflow: hidden;
								flex-direction: column;

								// display: none;

								li {
									height: 16px;
									position: relative;
									font-size: 12px;
									line-height: 16px;
									display: flex;
									padding-left: 12px;

									&::before {
										position: absolute;
										content: '';
										left: 0;
										top: 50%;
										-webkit-transform: translateY(-50%);
										-ms-transform: translateY(-50%);
										transform: translateY(-50%);
										width: 4px;
										height: 4px;
										border-radius: 4px;
										background: #FF2323;
										overflow: hidden;
									}

									&:not(:last-child) {
										margin-bottom: 8px;
									}
								}
							}
						}

						&__item.open {

							.button svg {
								transform: rotate(180deg);
							}

							.list {
								display: flex;
								height: 48px;
							}
						}
					}
				}

				&__bottom {
					display: flex;
					flex-direction: column;
					height: 144px;
					padding: 8px 24px 16px;
					background-color: #ffffff;
					
					&-price {
						display: flex;
						align-items: flex-start;

						h3 {
							font-size: 24px;
							line-height: 32px;
						}

						p {
							position: relative;
							margin-left: 8px;
							color: #656565;
							font-size: 14px;
							line-height: 20px;

							&::after {
								position: absolute;
								content: '';
								width: 100%;
								height: 1px;
								left: 0;
								top: 11px;
								background: #656565;
							}
						}
					}

					&-discont {
						width: fit-content;
						height: 22px;
						display: flex;
						align-items: center;
						justify-content: center;
						font-size: 12px;
						line-height: 16px;
						color: #ffffff;
						text-transform: uppercase;
						margin: 8px 0;
						padding: 0 8px;
						border-radius: 16px;
						background-color: #F01A67;
					}

					.button-gray {
						width: 100%;
					}
				}
			}

			.item.bg-2 {
				.item__top {
					background: url(../img/section3/bg2.jpg) 0 0 / cover repeat-y;
					background: -webkit-image-set(url(../img/section3/bg2.jpg) 1x) 0 0 / cover repeat-y;
					background: image-set(url(../img/section3/bg2.jpg) 1x) 0 0 / cover repeat-y;
				}
			}

			.item.bg-3 {
				

				.item__top {
					height: auto;
					background: #ffffff;
					color: #1A1A1A;
				}

				.item__top-accordion__item .button {
					background-color: #FAFAFA;

					span {
						color: #1A1A1A;
					}
				}
			}
		}

		&-items.item-2 {
			max-width: 620px;

			@include maxw991 {
				max-width: 100%;
			}
		}
	}

}