.footer {
	background-color: #ffffff;

	.container {
		display: flex;
		flex: 0 1 auto;
		flex-flow: row wrap;
	}

	&__left {
		padding-left: 70px;
		position: relative;
		margin-bottom: 68px;
		max-width: 25%;

		flex: 0 0 auto;
		padding-right: 20px;

		@include maxw750 {
			max-width: 100%;
			margin-bottom: 42px;
		}

		&::before {
			content: "";
			position: absolute;
			left: 0px;
			top: -5px;
			width: 50px;
			height: 50px;
			background: url(../img/ekat.svg) 0% 0% / cover no-repeat;
		}

		p {
			max-width: 322px;
			white-space: pre-wrap;
			font-size: 14px;
			color: rgb(151, 151, 151);
		}
	}

	&__right {
		margin-left: auto;
		position: relative;
		margin-bottom: 68px;
		max-width: 75%;

		flex: 0 0 auto;

		@include maxw750 {
			max-width: 100%;
			margin-bottom: 42px;
		}

		p {
			white-space: pre-wrap;
			font-size: 14px;
			color: rgb(151, 151, 151);
		}
	}
}