.button-red {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 50px;
	background: linear-gradient(179.99deg, rgb(227, 8, 19) 0.01%, rgb(190, 36, 30) 99.99%);
    border-radius: 4px;
    border: none;
    color: rgb(255, 255, 255) !important;
}

.button-gray {
	box-sizing: border-box;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 12px 24px;
    width: auto;
    max-width: 100%;
    border: none;
    border-radius: 12px;
    box-shadow: none;
    font: var(--medium);
    color: #181B21;
    fill: #181B21;
    stroke: #181B21;
    font-size: 16px;
    line-height: 24px;
    -webkit-letter-spacing: 0;
    -moz-letter-spacing: 0;
    -ms-letter-spacing: 0;
    letter-spacing: 0;
    text-align: center;
    white-space: nowrap;
    background-color: #EEEEEF;
    cursor: pointer;
    outline: none;
}