:root {
	--container: 1650px;

	--f-default: 'Aeroport', sans-serif;
	--c-black: rgb(26, 26, 26);
	--c-red: #ff0039;
	--transition: 250ms ease;

}

* {
	outline: none;
	border: 0;
	margin: 0;
	padding: 0;
}

html, body {
	// position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
}

a, button {
	transition: var(--transition);

	* {
		transition: var(--transition);
	}
}

b, strong {
	font-weight: 600;
}



li {
	list-style: none;
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

body {
	position: relative;
	font-size: 16px;
	font-family: var(--f-default);
	color: var(--c-black);
	min-width: 320px;
	background-color: #F4F4F4;
}



.container {
	max-width: var(--container);
	width: 100%;
	margin-left: auto;
    margin-right: auto;
    padding-left: 80px;
    padding-right: 80px;

	@include maxw1200 {
		padding-left: 16px;
		padding-right: 16px;
		min-width: 320px;
	}
}

.overflow-hidden {
	overflow: hidden;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.fz-30 {
	font-size: 30px !important;
}

