.group_card {
	padding-top: 80px;
	padding-bottom: 60px;

	background-color: #ffffff;

	@include maxw991 {
		padding-top: 40px;
		padding-bottom: 30px;
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			background-color: #e5e5e5;
			border-radius: 12px;
			padding: 20px 12px;
			display: flex;
			flex-direction: column;

			flex: 0 0 calc((100% - 40px) / 3);
			max-width: calc((100% - 40px) / 3);

			margin-right: 20px;
			margin-bottom: 20px;

			@include maxw991 {
				flex: 0 0 calc((100% - 20px) / 2);
				max-width: calc((100% - 20px) / 2);

				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			@include maxw540 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;
			}

			@include minw991 {
				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			

			&__image {
				max-width: 48px;
				margin-bottom: 20px;
			}

			&__title {
				margin-bottom: 20px;

				h3 {
					font-weight: bold;
				}
			}

			&__desc {
				p {
					font-size: 14px;
					line-height: 16px;
				}
			}
		}
	}
}